import React from "react";
import { NavLink } from "react-router-dom";
import s from './Navbar.module.css';

export const Navbar = () => {
    return(
        <div className={s.outterContainer}>
            <NavLink to="/catalog"></NavLink>
            <NavLink to="/contact"></NavLink>
            <NavLink to="/about"> </NavLink>
        </div>
    )
}